import http from '@/common/axios.js';

export default {
    mapfunc(positions, map) {
        
        //   
            var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png"; 
            
            for (var i = 0; i < positions.length; i ++) {
                
                //     
                var imageSize = new kakao.maps.Size(24, 35); 
                
                //       
                var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize); 
                
                //  
                var marker = new kakao.maps.Marker({
                    map: map, //   
                    position: positions[i].latlng, //   
                    title : positions[i].title, //  ,     
                    image : markerImage //   
                });	
            }
        
    },
    
    loadMap(container,x,y,level){
        // 
        var mapContainer = container; //   div  
        var mapOption = { 
            center: new kakao.maps.LatLng(x, y), //  
            level: level //   
        };

        var map = new kakao.maps.Map(mapContainer, mapOption); //  

        return map;
    },
    setMainMap: function(map){
        let x = 37.50395942713657;
        let y = 127.04282459768903;

        //    
        var markerPosition  = new kakao.maps.LatLng(x, y); 

        //  
        var marker = new kakao.maps.Marker({
            position: markerPosition
        });

        //     
        marker.setMap(map);

        var iwContent = `<div style="padding:3px 20px;">SSAFY <br><a href="https://map.kakao.com/link/map/,${x},${y}" style="color:blue; font-size:12px" target="_blank"></a> <a href="https://map.kakao.com/link/to/,${x},${y}" style="color:blue; font-size:12px" target="_blank"></a></div>`, //    HTML  document element 
            iwPosition = new kakao.maps.LatLng(x, y); //  

        //  
        var infowindow = new kakao.maps.InfoWindow({
            position : iwPosition, 
            content : iwContent 
        });
        
        //    .   marker     
        infowindow.open(map, marker);
    },
    changeMap: function(map, marker) {
        var moveLoc = marker.latlng;
        console.log(moveLoc);
        map.setCenter(moveLoc);
    },
}

